import { Component, OnInit } from '@angular/core';
import { CusViewHttpService } from '../../http/custom-view/custom-view.http.service';
import { SelectGroupEvalInfoBean } from '../bean/selectGroupEvalInfo.bean';
import { Message, ConfirmationService, ConfirmDialogModule } from 'primeng/primeng';
import { ActivatedRoute, Params, Router } from '@angular/router';
// import { CustGroupListBean } from './../bean/custom-group.bean';
import * as _ from 'lodash';
import { tableMessage } from 'app/@uisftech/common/constant/config';
import { CusOperationHttpService } from "app/pages/tzb/custom/http/custom-operation/custom-operation.http.service";
import { Commfunc } from "app/@uisftech/common/service/commonFun";
import { CommonHttpService } from '../../../common/http/common.http.service';
import { CustGroupListBean } from 'app/pages/tzb/custom/custom-group/bean/custom-group.bean';
import { FormGroup, FormBuilder, FormControl, Validators } from '@angular/forms';


@Component({
  selector: 'app-custom-information-display',
  templateUrl: './custom-information-display.component.html',
  styleUrls: ['./custom-information-display.component.scss'],
  providers: [CusViewHttpService, ConfirmationService, CommonHttpService],
})
export class CustomInformationDisplayComponent implements OnInit {

  //公共
  tableMesg: any = tableMessage;
  // 标题
  title: string = 'xxx客户群-xxx客户';
  title1: string = '基本信息';
  // 营销假数据
  items: any[];
  data1: any = {};
  //模态框
  detailDisplay: boolean = false;
  addUpHeader: string;
  // 新建联系计划模态框
  display: boolean = false;
  addDisplay: boolean = false;
  inValue: any = [];
  //中间量
  list: any;//后台返回的数据
  updateValue: any = [];
  //总页数
  total: string;
  //每页多少条
  pageSize: number = 10;
  // bean
  selectGroupEvalInfoBean: SelectGroupEvalInfoBean = new SelectGroupEvalInfoBean();//第三方评价

  // 一般中间量
  relTypeTemp: any = '';
  // 传参中间量
  readonly: any = '';
  groupName: any = '';
  groupId: any = '';
  data: any = new CustGroupListBean;
  tempData: any = '';
  // 组件参数
  msgs: Message[] = [];
  first1: any = 0;
  first2: any = 0;

  pageNum: string;

  canEdite: boolean = true;
  //
  pageNum2: any = 10;
  pageSize2: any = 1;
  // 码值
  isNot2: any = [
    { label: '请选择', value: '' },
    { label: '否', value: '0' },
    { label: '是', value: '1' },
  ]
  //获取所有码值
  code: any;
  userform: FormGroup; //校验
  relType: any;// = CustomGroupRelType;//关联关系
  idenType: any;// = CustomGroupIdenType;//证件类型
  groupCreditEval: any;// = GroupCreditEval;// 信用评价
  groupOfferLines: any;// = GroupOfferLinese;// 建议额度
  blackCustFlag: any;//黑名单标识
  blackFlag: boolean = false;

  custNo: string;//判断是否是临时客户号
  custNo2: string;//获取路由传的客户号
  constructor(
    public fb: FormBuilder,
    public routerInfo: ActivatedRoute,
    public cusViewHttpService: CusViewHttpService,
    public router: Router,
    public confirmationService: ConfirmationService,
    public httpService: CusOperationHttpService,
    public CommonHttpService: CommonHttpService,
    public commfunc: Commfunc,
  ) {
    this.codeValues() //调用方法，获取全部码值
    //获取所需要的码值,中括号中的变量是自己在原来码值表中的变量名！！！！
    this.relType = this.code['CustomGroupRelType'];
    this.idenType = this.code['CustomGroupIdenType'];
    this.groupCreditEval = this.code['GroupCreditEval']
    this.groupOfferLines = this.code['GroupOfferLinese']
    this.blackCustFlag = this.code['blackCustFlag']

    this.userform = this.fb.group({
      'description': new FormControl('', [Validators.maxLength(1000)])
    });
  }
  //码值
  codeValues() {
    this.commfunc.codeValue();
    let ret = this.commfunc.codeDatas;
    return this.code = ret //code是自己声明的变量
  }
  ngOnInit() {
    this.preSet();
    // 接受客户名单信息
    // this.items = this.routerInfo.snapshot.params['data'];
  }
  preSet() {
    // 初始化
    // 路由传参
    if (this.routerInfo.snapshot.params.pageNum) {
      this.pageNum = this.routerInfo.snapshot.params.pageNum;
    }
    if (this.routerInfo.snapshot.params.readonly) {
      this.readonly = this.routerInfo.snapshot.params.readonly;
    }
    if (this.routerInfo.snapshot.params.groupId) {
      this.groupId = this.routerInfo.snapshot.params.groupId;
    }

    if (this.routerInfo.snapshot.params.custNo) {
      this.custNo2 = this.routerInfo.snapshot.params.custNo;
    }
    this.selectCustGroupInfo(this.groupId, this.custNo2);//查询群信息
  }
  // 判断是否临时客户
  isQl(col) {
    if (col.custNo.search(/^QL/)) {
      return true;
    }
    return false;
  }
  //群基本信息查询
  selectCustGroupInfo(groupId, custNo) {
    let params = {
      groupId: groupId
    }
    this.httpService.selectCustGroupInfo(params).subscribe(data => {
      if (data.returnCode.code == 'success') {
        this.groupName = data.groupName;
        this.selectCustGroupList(custNo);//查询客户信息
      } else {
        this.msgs = [];
        data.returnCode.code ? data.returnCode.code : '调用服务失败！';
        this.msgs = [{ severity: 'error', summary: '提示', detail: data.returnCode.message }]
      }
    }, err => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' })
    })
  }
  //群客户名单查询
  selectCustGroupList(custNo) {
    let params = {
      custNo: custNo,
      groupId: this.groupId
    }
    this.httpService.selectCustGroupList(params).subscribe(data => {
      if (data.returnCode.code == 'success') {
        this.data.custMap = data.custList[0];
        console.log(this.data.custMap)
        if (this.isQl(this.data.custMap)) {//判断临时客户号
          this.custNo = this.data.custMap.custNo;
        } else {
          this.custNo = '';
        }

        this.selectGroupEvalInfoBean.pageSize = 10;
        this.selectGroupEvalInfoBean.pageNum = 1;
        this.title = `${this.groupName}客户群-${this.data.custMap.custName}客户`;
        if (this.data.custMap && this.relType) {
          this.relType.forEach(item => {
            if (item.value === this.data.custMap.relType) {
              this.relTypeTemp = item.label;
              return;
            }
          })
        }

        if (this.data.custMap.blackCustFlag != '1') {
          this.blackFlag = true;
        } else {
          this.blackFlag = false;
        }

        this.copy();
      } else {
        this.msgs = [];
        data.returnCode.code ? data.returnCode.code : '调用服务失败！';
        this.msgs = [{ severity: 'error', summary: '提示', detail: data.returnCode.message }]
      }
    }, err => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' })
    })
  }
  //获取userame
  getUserName(userId, index) {
    let params = {
      tellerId: userId
    }
    let successFnc = data => {
      this.selectGroupEvalInfoBean.evalList[index]["evalName"] = data.tellerName || '';
      this.selectGroupEvalInfoBean.evalList[index]["partyName"] = this.data.custMap.custName || '';
      this.updateValue = _.clone(this.updateValue);
    }
    this.commfunc.interfaceComFnc(this.CommonHttpService.queryTellerById(params), successFnc, this.msgs)

  }

  //初始
  copy() {
    // debugger
    this.selectGroupEvalInfoBean["readonly"] = this.readonly;
    this.selectGroupEvalInfoBean.groupName = this.groupName;
    this.selectGroupEvalInfoBean.custNo = this.data.custMap.custNo;//客户维度
    this.selectGroupEvalInfoBean.handleFlag = '2';
    this.selectGroupEvalInfoBean.pageNum = 1;
    this.selectGroupEvalInfoBean.pageSize = 10;

    let params = {
      handleFlag: 2,
      groupId: this.groupId,
      custNo: this.data.custMap.custNo,
      pageNum: this.selectGroupEvalInfoBean.pageNum,
      pageSize: this.selectGroupEvalInfoBean.pageSize,
    }
    let ob = this.cusViewHttpService.selectGroupEvalInfo(params);
    ob.subscribe(data => {
      if (data.returnCode.code == 'success') {
        this.selectGroupEvalInfoBean = data;
        this.selectGroupEvalInfoBean.evalList = data.evalList;

        // this.selectGroupEvalInfoBean.evalList.forEach((element,i) => {
        //   this.selectGroupEvalInfoBean.evalList[i].fromDate = element.fromDate.split(' ')[0];
        // });
        // 
        this.list = data.evalList;
        this.total = data.totalNum;
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败' });
    })
  }
  //详情
  checkDetail(col) {
    this.detailDisplay = true;
    this.addUpHeader = '第三方评价详情';
    this.updateValue = col;
    document.body.style.overflow = "hidden";
  }
  //详情的回调
  detailsCall(param) {
    this.detailDisplay = param;
    document.body.style.overflow = 'auto';
  }
  openAppraiser() {
    // 打开第三方评价
    this.confirmationService.confirm({
      message: '确定进入第三方评价页面?',
      accept: () => {
        this.router.navigate(['/pages/tzb/custom/custom-group/third-party-evaluation/appraiser-evaluate', { readonly: this.readonly, groupName: this.groupName, customInfoFlag: 1, data: this.tempData }]);
      }
    });

  }
  //新建联系计划
  newPlan() {
    this.addUpHeader = '新建客户联系计划';
    this.display = true;
    this.addDisplay = true;
    let arr = [];
    arr = [
      {
        custNo: this.data.custMap.custNo,
        custName: this.data.custMap.custName
      }
    ];
    this.inValue = _.clone(arr);
    document.body.style.overflow = "hidden";
  }

  //新建联系计划-关闭模态框
  newPlanCall(event) {
    this.addDisplay = false;
    this.display = false;
    document.body.style.overflow = 'auto';
  }
  // //分页
  // paginate(event) {
  //   //每页显示的条数
  //   this.selectGroupEvalInfoBean.pageSize = event.rows;
  //   //当前页
  //   let currentPage = event.page + 1;
  //   this.selectGroupEvalInfoBean.pageNum = currentPage;
  //   //调用初始方法
  //   this.copy();
  // }
  itemPaginate(event) {
    // 营销分页
    this.data1.pageSize = event.rows;
    let currentPage = event.page + 1;
    this.data1.pageNum = currentPage;
    this.first1 = (currentPage - 1) * this.data1.pageSize;
    //营销查询--待修改2018.1.5
  }
  evalPaginate(event) {
    // 第三方评价分页
    this.selectGroupEvalInfoBean.pageSize = event.rows;
    let currentPage = event.page + 1;
    this.selectGroupEvalInfoBean.pageNum = currentPage;
    this.first2 = (currentPage - 1) * this.selectGroupEvalInfoBean.pageSize;
    this.copy();//客户群名单查询
  }
  onHidden() {
    // 退出模态框
    document.body.style.overflow = 'auto';
  }
  // 其他
  returnGroupView() {
    // 返回客户群视图
    this.confirmationService.confirm({
      message: '确定返回客户群视图?',
      accept: () => {
        let groupId = this.commfunc.compileStr(this.groupId);
        this.router.navigate(['/pages/tzb/custom/custom-group/custom-group-view', { groupId: groupId, readonly: this.readonly, groupName: this.groupName, pageNum: this.pageNum }]);
      }
    });
  }
  editInfo() {
    this.canEdite = false;
  }
  savaInfo() {
    //校验的
    for (const i in this.userform.controls) {
      this.userform.controls[i].markAsDirty();
    }
    //处理是否提交
    for (const i in this.userform.controls) {
      if (!this.userform.controls[i].valid) {
        return;
      }
    }

    this.canEdite = true;
    let params = {
      groupId: this.groupId,
      custListNum: 1,
      custMap: this.data.custMap,
      flagId: '1'
    }
    this.httpService.updateCustGroupList(params).subscribe(data => {
      if (data.returnCode.code == 'success') {
        this.selectGroupEvalInfoBean = data;
        this.selectCustGroupList(this.custNo2);
        // 旧
        this.list = data.evalList;
        this.total = data.totalNum;
        this.msgs = [];
        this.msgs.push({ severity: 'success', summary: '提示', detail: "修改成功！" });
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败' });
    })
  }
  toCustomInfo(col) {
    // 去客户信息页面
    // this.commfunc.setSessionData('custNo', col);
    if (col.doorNum != '' && col.doorNum) {
      let data = JSON.stringify(col)
      this.router.navigate(['pages/tzb/custom/custom-group/household-information-display', { groupId: this.groupId, groupName: this.groupName, data: data }]);
    }
  }
  jumpToEv() {
    if (this.data.custMap.relType == 1) {
      let param = {
        groupId: this.groupId,
        groupName: this.groupName,
        custNo: this.data.custMap.custNo,
        doorId: this.data.custMap.doorNum,
        custGroupView: '1',
        customInfo: JSON.stringify(this.data.custMap),
      }
      this.router.navigate(['/pages/tzb/custom/custom-group/third-party-evaluation/custom-evaluate', param]);
    } else {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: '您好，第三方评价需要与户主关系为本人！' });
    }
  }
  //按钮权限
  permissionCheck(id) {
    return this.commfunc.permissionCheck(id)
}



}
