import { Component, OnInit, Output, EventEmitter, Input } from '@angular/core';
// import {ActivatedRoute} from "@angular/router";
import { CusViewHttpService } from '../../../http/custom-view/custom-view.http.service';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';


@Component({
  selector: 'app-custom-display-detail',
  templateUrl: './custom-display-detail.component.html',
  styleUrls: ['./custom-display-detail.component.scss'],
  providers: [CusViewHttpService]
})
export class CustomDisplayDetailComponent implements OnInit {
  @Output() outValue: EventEmitter<any> = new EventEmitter();
  @Input() inValue: any;

  items: any = [];
  // 码值码值
  code:any;
  groupHealthFlag: any;// = GroupHealthFlag;// 健康状况
  groupCreditEval: any;// = GroupCreditEval;// 信用评价
  groupSourceOfIncome: any ;//= GroupSourceOfIncome;// 收入来源
  groupFamilyStableFlag: any ;//= GroupFamilyStableFlags;// 家庭关系是否稳定
  groupBadHabits: any ;//= GroupBadHabits;// 不良嗜好
  groupCarFlag: any ;//= GroupCarFlag;// 车辆状况
  groupIncomeLevel: any;// = GroupIncomeLevel; // 家庭年收入级别
  groupOfferLines: any;// = GroupOfferLinese;// 建议额度

  constructor(
    public cusViewHttpService: CusViewHttpService,
    public commfunc: Commfunc,
  ) { this.codeValues() //调用方法，获取全部码值
    //获取所需要的码值,中括号中的变量是自己在原来码值表中的变量名！！！！
    this.groupHealthFlag = this.code['GroupHealthFlag']; 
    this.groupCreditEval = this.code['GroupCreditEval'];
    this.groupSourceOfIncome = this.code['GroupSourceOfIncome']
    this.groupFamilyStableFlag = this.code['GroupFamilyStableFlags']; 
    this.groupBadHabits = this.code['GroupBadHabits'];
    this.groupCarFlag = this.code['GroupCarFlag']
    this.groupIncomeLevel = this.code['GroupIncomeLevel']; 
    this.groupOfferLines = this.code['GroupOfferLinese'];
  }

  ngOnInit() {

  }
  codeValues() {
    this.commfunc.codeValue();
    let ret = this.commfunc.codeDatas;
    return this.code= ret //code是自己声明的变量
    }
  //关闭
  close() {
    this.outValue.emit(false);
  }
}
